<template>
	<view class="follow">
		<view class="follow-list">
			<view @click="gotoUserDetails(item.userId)" v-for="(item,index) in fansList " class="item">
				<listItem showRight>
					<template slot="left">
						<view style="display: flex;align-items: center;">
							<image  style="width: 50px;height: 50px;margin-right: 10px;border-radius: 50%;" :src="setUrl(item.avatar)"></image>
							<view class="info">
								<span>{{item.nickName}}</span>
								<view style="margin-top: 5px;" class="sex">
									<image v-if="item.sex==1" style="width: 12px;height: 12px;margin-left: 3px;" src="../../static/images/icon/g (1).png"></image>
									<image v-if="item.sex==0" style="width: 12px;height: 12px;margin-left: 3px;"src="../../static/images/icon/b.png"></image>
								</view>
							</view>
						</view>
					</template>
				</listItem> 
			</view>
		</view>
	
		<myLoad :status="status"></myLoad>
	</view>
</template>

<script>
	import listItem from "@/components/listItem/listItem.vue"
	import {getFansListByUserId} from "@/api/user.js"
	export default {
		data() {
			return {
				queryParams:{
					pageNum: 1,
					pageSize: 5,
					userId:null
					
				},
				status:"more",
				fansList:[]
			}
		},
		components:{
			listItem
		},
		onLoad(op) {
			if(op.userId!=null){
				this.queryParams.userId = op.userId
				this.getFansList();
			}
				
		},
		methods: {
			gotoUserDetails(id){
				uni.navigateTo({
					url:`/pages/userDetail/userDetail?userId=${id}`
				})
			},
			setUrl(url){
				return this.getUrl(url)
			},
			getFansList(){
				this.status = "loading"
				getFansListByUserId(this.queryParams).then(res=>{
					
					if(res.data.rows==null||res.data.rows.length==0){
						this.status = "nomore";
						return;
					}
					else{
						this.status = "more";			
						this.fansList = [...this.fansList,...res.data.rows]
					}
				})
			},
			//分页
			handleCurrentChange(){
				
				if(!(this.status == "more")){
					return ;
				}
				// Vue.set(this.queryParams,pageNum,page.pageNum)
				this.queryParams.pageNum++;
				this.queryParams.pageSize;
				this.getFansList();
			},
		},
		//触底
		onReachBottom(){
			this.handleCurrentChange();
		}
	}
</script>

